* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  font-family: Helvetica;
}

body {
  margin: 0px;
  padding: 0px;
}
.rm {
  padding: 0%;
  margin: 0%;
}

.rotate {
  position: fixed;
  left: 30%;
  top: 15%;
  width: 40%;
  height: 60%;
  animation: rotation 3s infinite linear;
}

@keyframes rotation {
  from {
    transform: rotate(359deg);
  }
  to {
    transform: rotate(0deg);
  }
}


/* Navbar */

nav {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 0px;
  font-family: verdana;
  font-size: 18px;
  background-color: rgb(255, 255, 255);
}

nav div ul li {
  padding-left: 25px;
  display: inline;
}

hr {
  color: rgb(138, 222, 233);
  background-color: rgb(161, 206, 233);
}

.ver-center {
  display: flex;
  align-items: center;
}

.nav-button {
  margin-left: auto;
  font-size: 2.5em;
  color: rgb(250, 250, 250);
  background-color: rgb(15, 0, 0);
  border-radius: 5px;
  padding: 1% 2% 0% 2%;
}

a {
  color: rgb(8, 5, 15);
  text-decoration: none;
}
a:hover {
  color: rgb(55, 80, 190);
}

.cool-link {
  display: inline-block;
}

.cool-link::after {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  background: rgb(17, 49, 90);
  transition: width 0.3s;
}

.cool-link:hover::after {
  width: 100%;
}

.container {
  display: flex;
  flex-wrap: wrap;
  gap: 2%;
  padding: 2% 10%;
}
.container-nav {
  display: flex;
  flex-wrap: wrap;
  gap: 2%;
}

.vertical-center {
  display: flex;
  align-items: center;
}
.vertical-stretch {
  display: flex;
  align-items: stretch;
}
.gap-2 {
  gap: 2%;
}
.gap-5 {
  gap: 5%;
}

.col-2 {
  width: 16%;
}
.col-6 {
  width: 48%;
}
.col-8 {
  width: 65%;
}
.col-12 {
  width: 99%;
}
.col-4 {
  width: 32%;
}
.col-3 {
  width: 23%;
}

.flex-center {
  display: flex;
  justify-content: center;
}
.flex-end {
  display: flex;
  justify-content: flex-end;
}
.flex-right {
  display: flex;
  justify-content: flex-start;
}

.intro-image {
  width: 100%;
  height: auto;
}
.on-image {
  position: absolute;
  top: 20%;
  left: 10%;
  padding: 3%;
  background-color: rgba(54, 153, 211, 0.3);
  color: whitesmoke;
  border-radius: 15%;
  width:36%;
}

 
.slide {
  
}
.zoom {
  transition: transform 0.3s; /* Animation */
}
.zoom:hover {
  transform: scale(1.03);
}

.button {
  border-radius: 5px;
  padding: 12px 12px;
  font-size: 15px;
}
.button:hover {
  cursor: pointer;
}


.center-text {
  text-align: center;
}

.card {
  border: solid 0.5px rgb(221, 220, 220);
  border-radius: 5px;
  padding: 0.5%;
  padding-bottom: 2%;
  margin-bottom: 1%;
  text-align: center;
}

.shadow {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.card-container {
  border: solid 0.5px rgb(212, 211, 211);
  border-radius: 8px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 2px 5px 0 rgba(0, 0, 0, 0.19);
}

.pagination > button {
  background-color: #ffffff;
  color: #36a3ec;
  padding: 10px;
  border: 1px solid rgb(221, 223, 224);
  cursor: pointer;
}

.pagination > button:hover {
  color: #ffffff;
  background-color: rgb(17, 95, 134);
  border-color: rgb(95, 119, 185);
}
.pagination > .disabled {
  background-color: #ced3d6 !important;
  border: solid 1px #c3c5c5 !important;
  color: #36a3ec !important;
  cursor: none;
  pointer-events: none;
}
.pagination > .active {
  color: white !important;
  background-color: #649abe !important;
  border: solid 1px #23acce !important;
}

.search,
.input {
  background: #fff;
  padding: 12px 20px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
