@media (max-width: 800px) {
  nav {
    display: block;
    padding: 0px;
  }
  nav div ul li {
    display: block;
    padding: 5px;
  }
  .cool-link {
    display: block;
  }
  .ver-center {
    padding-right: 10px;
  }
  .flex-mobile-right {
    display: flex;
    justify-content: flex-start;
  }
  .rotate {
    left: 10%;
    top: 30%;
    width: 80%;
    height: 32%;
  }
  .search {
    width: 70%;
  }
  .col-6,
  .col-4,
  .col-3,
  .col-2,
  .col-8 {
    width: 96%;
  }

  .card-mobile {
    margin: 6px;
    padding: 10px 20px;
  }

  .col-6-mobile {
    width: 48%;
    padding-bottom: 1%;
    margin-bottom: 1%;
    text-align: center;
  }
  .p-2-m {
    padding: 2%;
  }
  .pl-10-m {
    padding-left: 10%;
  }
  .pc {
    display: none;
  }
  .mobile {
    display: block;
  }
}
